/* LoginForm.module.css */
.formContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f8f8f8;
}

.formGroup {
    margin-bottom: 15px;
}

.formLabel {
    font-weight: bold;
}

.formInput {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #fff;
    box-sizing: border-box;
}

.formCheckbox {
    margin-right: 5px;
}

.formSummaryError {
    color: red;
    margin-top: 10px;
}

.formButton {
    background-color: #1877F2;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s;
}

.formButton:hover {
    background-color: #135EAC;
}

.formCaptchaImage {
    margin-top: 10px;
    max-width: 100%;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.formCaptchaInput {
    margin-top: 10px;
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #fff;
    box-sizing: border-box;
}
